import { Component, ElementRef, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TrainsResponseSection } from '@schemas/modules/trains/train.response';
import { SelectedOption, ShowTrainDetail, TrainSelection } from '@schemas/modules/trains/train-search-query-params';
import { TRAIN } from '@schemas/modules/trains/train';
import { PlatformService } from '@services/platform.service';
import { ServiceListComponent } from '@core/components/paginate.component.ts/service.list.component';
import { IInfiniteScrollEvent } from 'ngx-infinite-scroll';
import { ScrollService } from '@services/modules/core/scroll-service';

@Component({
    selector: 'tz-trains-list',
    templateUrl: './trains-list.component.html',
    styleUrls: ['./trains-list.component.scss'],
})
export class TrainsListComponent extends ServiceListComponent implements OnInit {
    @Input() section!: TrainsResponseSection;
    @Input() selectedOption!: SelectedOption;
    @Input() showTrainDetail!: ShowTrainDetail;
    @Input() isListCompleted = false;
    @Input() isRoundTrip = false;
    @Input() isChangeOptions = false;
    @Input() hideTitle = false;
    @Input() totalPassengers = 0;
    @Input() confirmable = false;
    @Output() mobShowTrainDetails: EventEmitter<TRAIN> = new EventEmitter<TRAIN>();
    @Output() nextPage: EventEmitter<void> = new EventEmitter<void>();
    @Output() selectedTrainChange: EventEmitter<TrainSelection> = new EventEmitter<TrainSelection>();
    @Output() selectedClassChange: EventEmitter<TrainSelection> = new EventEmitter<TrainSelection>();
    @Output() trainDetailClosed: EventEmitter<string> = new EventEmitter<string>();

    public fromLocation: string | undefined;
    public toLocation: string | undefined;
    public sectionSelected = false;
    public trainType: 'depart' | 'return' = 'depart';
    constructor(public platformService: PlatformService, private scrollService: ScrollService) {
        super();
    }

    ngOnInit(): void {
        this.setSectionInfo();
    }

    public identify(index: number, item: TRAIN): string {
        return item.id;
    }

    public onShowTrainDetails(train: TRAIN): void {
        this.mobShowTrainDetails.emit(train);
    }

    public onScroll($event: IInfiniteScrollEvent) {
        if (!this.isListCompleted) {
            if (this.section.limiter !== 1) {
                this.nextPage.emit();
            }
        }
    }

    public onTrainListItemClicked(trainListItem: any): void {
        if (!this.confirmable) {
            requestAnimationFrame(() => {
                this.scrollService.scrollToElement(trainListItem);
            });
        }
    }

    private setSectionInfo(): void {
        const option =
            this.section.section === 'onwardJourney' ? this.selectedOption.onward : this.selectedOption.return;
        if (option) {
            this.sectionSelected = option.selected;
            this.fromLocation = option.fromLocation;
            this.toLocation = option.toLocation;
            this.trainType = 'return';
        }
    }
}
