import { Component, ElementRef, Injector, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';

import { BaseServiceComponent } from '../../components/base-service-component/base-service.component';
import { ServiceId } from '@core/constants/service-id.const';
import { FilterReferenceItem } from '@schemas/modules/core/filters/service-filter';
import {
    SelectedOption,
    ShowTrainDetail,
    TrainSearchQueryParams,
    TrainSelection,
} from '@schemas/modules/trains/train-search-query-params';
import { ITrainsCartData } from '@schemas/modules/trains/train-cart-data';
import { TrainListMetadata } from '@schemas/modules/trains/train-list-metadata';
import { FlightTrainSearchService, IHasTransport } from '@services/services/flight-train-search.service';
import { TrainsResponse, TrainsResponseSection } from '@schemas/modules/trains/train.response';
import { SortByValueOption } from '@schemas/modules/core/sort-by-value.option';
import { ListenerId } from '@core/constants/listener-id.const';
import { Trip } from '@core/constants/trip';
import { StateFrom } from '@core/constants/state-from.const';
import { TrainService } from '@services/modules/transport/trains/trains.service';
import { ICart } from '@schemas/modules/cart/cart';
import { finalize, skip, takeUntil, tap } from 'rxjs/operators';
import { TrainClass } from '@schemas/modules/trains/train-class';
import { TrainSort } from './train.sort';
import { TrainUtilsService } from '@services/modules/transport/trains/train.utils';
import { TRAIN } from '@schemas/modules/trains/train';
import { MobTrainDetailsComponent } from './components/mob-train-details/mob-train-details.component';
import { FilterService } from '@services/modules/inactivity/filter.service';
import { SearchUtilsService } from '@services/modules/search/utils/search.utils';
import { ResultsFiltersUtils } from '../../results-filters/results-filters.utils.service';

@Component({
    selector: 'tz-trains',
    templateUrl: './trains.component.html',
    styleUrls: ['./trains.component.scss'],
})
export class TrainsComponent extends BaseServiceComponent implements OnInit, OnDestroy {
    @ViewChild('confirmationComponent') confirmationComponent?: ElementRef;
    @ViewChild('trainList') trainListElementReference?: ElementRef;
    @ViewChild('trainListSkeletonLoader') trainListSkeletonLoaderElementReference?: ElementRef;
    public readonly serviceId = ServiceId.TRAIN;
    public trainListMetadata: TrainListMetadata = {};
    public trainSections: TrainsResponseSection[] = [];
    public trainCartData: ITrainsCartData | undefined;
    public trainsFilter: FilterReferenceItem = [];
    public journeyType: Trip;
    public twoWayFlight = false;
    public twoTransports: IHasTransport = { has: null };
    public selectedOption: SelectedOption = {};
    public showDetails: ShowTrainDetail = {
        onwardJourney: false,
        returnJourney: false,
    };
    public showSidebar = false;
    public sortByOptions: SortByValueOption[] = TrainSort;
    public filterQueryParams: TrainSearchQueryParams = {};
    public isRoundTrip = false;
    public isChangeOptions = false;
    public serviceTitle = '';
    public totalPassengers = 0;
    public selectedFiltersCount = 0;

    private editListener = -1;
    private repriceListenerId = -1;
    private resultsCount$ = new Subject<number>();
    private returnTrainChange$ = new Subject<TRAIN>();

    constructor(
        private trainService: TrainService,
        private flightTrainSearchService: FlightTrainSearchService,
        private injector: Injector,
        private trainUtilsService: TrainUtilsService
    ) {
        super(injector);
        this.journeyType = this.searchDataService.travelType;
        this.isMobile$ = this.screenSizeDetectorService.isMobile;
        this.isNextSummary = this.nextIsSummary();
        this.flightTrainSearchService.twoTransports.pipe(takeUntil(this.destroy$)).subscribe((twoTransports) => {
            console.log('got:', twoTransports);
            this.twoTransports = twoTransports;
        });
    }

    get isEnableClearFilter(): boolean {
        return FilterService.enableClearAll(this.trainsFilter);
    }

    ngOnInit(): void {
        this.scrollToTop();
        this.initializeTrainComponent(true);
        if (this.searchRunning()) {
            this.showSkeletonLoader(true);
        } else {
            this.loadTrainsData(true);
        }

        this.trainService.updateTrainSelection.subscribe((trainSelection) => {
            this.mobUpdateTrainSelection(trainSelection);
        });
    }

    ngOnDestroy(): void {
        this.trainService.setTrainSelections(this.selectedOption);
        this.listenersService.unregisterListener(this.editListener);
        this.listenersService.unregisterListener(this.repriceListenerId);
        this.destroy$.next(false);
        this.destroy$.complete();
    }

    public onFilterChanged($event: FilterReferenceItem): void {
        this.scrollToServiceContainer();
        this.resetPaginationParameters();
        this.trainsFilter = $event;
        this.filterTrains().subscribe(() => {});
    }

    public onOpenFilterPopup() {
        const filter: BehaviorSubject<FilterReferenceItem> = new BehaviorSubject(this.trainsFilter);
        // eslint-disable-next-line @typescript-eslint/no-shadow
        filter.pipe(takeUntil(this.destroy$)).subscribe((filter: FilterReferenceItem) => {
            this.trainsFilter = filter;
            this.filterTrains().subscribe(() => {});
        });
        this.openFilterDialog(filter, this.resultsCount$);
        this.showSidebarMobile();
    }

    public sortTrains(sortValue: string): void {
        this.resetPaginationParameters();
        SearchUtilsService.saveSortingValue(ServiceId.TRAIN, sortValue);
        this.filterQueryParams.sort_by = sortValue;
        this.loadTrainsData();
    }

    public onSubmitBooking(): void {
        this.trainService.addTrainToCart(this.selectedOption).subscribe(
            (response: ICart) => {
                this.selectedOption = {};
                this.userSearchService.searchCheckDep(ServiceId.TRAIN, this.nextIsSummary(), false, false, true);
            },
            (error) => {
                //this.reInit();
                console.log(error);
            }
        );
    }

    public onCancelSelection(): void {
        this.scrollToServiceContainer();
        this.trainService.clearTrainsSelection();
        this.selectedOption = {};
        this.reInit();
    }

    public onTrainSelection(selectedTrain: TrainSelection): void {
        this.scrollToServiceContainer();
        this.handleClassChangeOfSelectedTrain(selectedTrain);
    }

    public onClassSelection(selectedTrain: TrainSelection): void {
        if (selectedTrain.selected) {
            this.handleClassChangeOfSelectedTrain(selectedTrain);
            return;
        }

        if (selectedTrain.journeyType === 'onwardJourney') {
            this.onOnwardChange(selectedTrain);
        } else {
            this.onReturnChange(selectedTrain);
        }
    }

    public onTrainDetailClosed(journeyType: string) {
        if (journeyType === 'onwardJourney') {
            this.showDetails.onwardJourney = false;
        } else {
            this.showDetails.returnJourney = false;
        }
    }

    public onRemovedSelectedTrain(optionId: string | undefined): void {
        this.removeService(this.serviceId, optionId).then(
            (isRemoved: boolean) => {
                console.log('remove success');
            },
            (error) => {
                console.error(error);
            }
        );
    }

    public getTrainTitle(): string {
        return this.trainUtilsService.getSelectedTitle(this.trainCartData);
    }

    public onChangeOptions(): void {
        if (this.trainCartData && this.trainCartData.items) {
            this.filterQueryParams.journey_id = this.trainCartData.items[0].selectedItem.data.onward.groupId;
            this.filterQueryParams.onwardId = this.trainCartData.items[0].selectedItem.id;
            if (this.isRoundTrip && this.trainCartData.items.length > 1) {
                this.filterQueryParams.returnId = this.trainCartData.items[1].selectedItem.id;
            }
            this.isChangeOptions = true;

            this.filterTrains(true).subscribe(() => {
                if (this.platformService.isMobileDevice) {
                    this.mobChangeOptions();
                }
            });
        }
    }

    public onChangeTickets() {
        this.scrollToTrainsList();
    }

    public onOpenFilterModal(): void {
        const filters: BehaviorSubject<FilterReferenceItem> = new BehaviorSubject(this.trainsFilter);
        filters.pipe(takeUntil(this.destroy$)).subscribe((filter: FilterReferenceItem) => {
            this.trainsFilter = filter;
            this.filterTrains().subscribe(() => {});
        });
        this.openFilterModal(filters, this.resultsCount$);
    }

    public onResetFilter(): void {
        const filters$: Subject<FilterReferenceItem> = new Subject();
        filters$.pipe(takeUntil(this.destroy$)).subscribe((filter: FilterReferenceItem) => {
            this.trainsFilter = filter;
            this.filterTrains().subscribe(() => {});
        });
        BaseServiceComponent.resetFilter(this.trainsFilter, filters$);
    }

    public mobOpenSort(): void {
        const sort$: Subject<string> = new Subject<string>();
        sort$.pipe(takeUntil(this.destroy$)).subscribe((sortKey: string) => {
            this.sortTrains(sortKey);
        });

        this.openSortMob(this.sortByOptions, this.filterQueryParams.sort_by, sort$);
    }

    public async mobShowTrainDetails(train: TRAIN): Promise<void> {
        const onwardTrain =
            this.selectedOption.onward && train.id !== this.selectedOption.onward.id
                ? this.trainSections[0].data[0]
                : null;

        const modal = await this.modalService.createDetailsPage(MobTrainDetailsComponent, {
            data: { train, selectedOption: this.selectedOption, onwardTrain, passengersNumber: this.totalPassengers },
            title: 'FLIGHT_TRAIN.CHOOSE_DEPART',
            isHeaderBottomDivider: false,
        });

        if (modal.data) {
            this.onTrainSelection(modal.data.selectedTrain);

            if (modal.data.isConfirmable || !this.twoWayFlight) {
                this.onSubmitBooking();
            }
        }
    }

    public nextPage(section: TrainsResponseSection): void {
        if (!this.loading) {
            this.loading = true;
            const { onwardId, returnId } = this.filterQueryParams;
            const params = {
                onwardId,
                returnId,
                limit: section.increment,
                start: onwardId !== '' ? section.data.length + 1 : section.data.length,
                sortBy: this.filterQueryParams.sort_by,
            };
            this.trainService
                .paginate(this.serviceId, params)
                .pipe(
                    finalize(() => {
                        this.loading = false;
                    })
                )
                .subscribe((trains: TRAIN[]) => {
                    section.data = section.data.concat(trains);
                    if (trains.length < section.increment) {
                        this.isListCompleted = true;
                    } else {
                        this.start += section.limiter;
                    }
                });
        }
    }

    private mobChangeOptions(): void {
        const data: any = {
            train: this.trainSections[0].data[0],
            selectedOption: null,
            isCartItems: true,
            passengersNumber: this.totalPassengers,
            returnTrainChange$: this.returnTrainChange$,
        };

        if (this.isRoundTrip && this.trainCartData && this.trainCartData.items.length > 1) {
            data.onwardTrain = this.trainSections[1].data[0];
        }

        this.modalService
            .createDetailsPage(MobTrainDetailsComponent, {
                data,
            })
            .then((modal: { data: any }) => {
                if (modal.data) {
                    if (!(this.isRoundTrip && this.trainCartData && this.trainCartData.items.length > 1)) {
                        this.onTrainSelection(modal.data.selectedTrain);
                    }

                    if (modal.data.selectedOption) {
                        this.onTrainSelection(modal.data.selectedOption.return);
                    }

                    if (modal.data.isConfirmable || !this.twoWayFlight) {
                        this.onSubmitBooking();
                    }
                }

                this.onCancelSelection();
            });
    }

    private mobUpdateTrainSelection(selectedTrain: TrainSelection): void {
        this.onTrainSelection(selectedTrain);

        if (this.isRoundTrip && this.trainCartData && this.trainCartData.items.length > 1) {
            this.returnTrainChange$.next(this.trainSections[1].data[0]);
        }
    }

    private handleExtraParams() {
        const state = history.state;
        if (state && state.extra) {
            for (const extraAction in state.extra) {
                if (state.extra.hasOwnProperty(extraAction)) {
                    switch (extraAction) {
                        case 'changeOptions':
                            this.onChangeOptions();
                            break;
                        case 'changeTickets':
                            this.reInit();
                            this.scrollToTrainsList();
                            break;
                    }
                }
            }
        }
    }

    private initializeTrainComponent(isInit: boolean): void {
        this.twoWayFlight = this.journeyType === Trip.ROUND_TRIP;
        this.initSearchParams();
        this.getCartServiceData();
        if (this.shouldPreserveSelection()) {
            if (!this.trainCartData?.items.length) {
                this.loadSelection();
            }
        }
        this.setSortingValue();

        if (isInit) {
            this.editListener = this.listenersService.registerListener(
                ListenerId.LISTEN_EDIT_TRAIN,
                this.loadTrainsData.bind(this, false),
                this
            );
            this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
                this.initializeTrainComponent(false);
            });
            this.currencyService.selectedCurrencySubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
                this.loadTrainsData(false);
            });

            this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
                this.serviceTitle = this.formServiceTitle(ServiceId.FLIGHT_TRAIN, this.twoWayFlight, this.serviceId);
                this.loadTrainsData(false);
            });

            this.repriceListenerId = this.listenersService.registerListener(
                ListenerId.LISTEN_REPRICED,
                this.getCartServiceData,
                this
            );
        }
    }

    private reInit() {
        this.isChangeOptions = false;
        this.trainService.setTrainSelections({});
        if (this.shouldPreserveSelection()) {
            this.loadSelection();
        }
        /* this.filterQueryParams.journey_id = '';
        this.filterQueryParams.onwardId = '';
        this.filterQueryParams.returnId = '';*/
        this.initSearchParams();
        this.filterQueryParams.refreshFilter = true;
        this.filterTrains().subscribe(() => {});
    }

    private trainSelection(selectedTrain: TrainSelection): void {
        if (selectedTrain.selected) {
            this.filterQueryParams.journey_id = selectedTrain.groupId;
        }

        this.filterQueryParams.refreshFilter = true;
        if (selectedTrain.journeyType === 'onwardJourney') {
            this.onOnwardChange(selectedTrain);
        } else {
            this.onReturnChange(selectedTrain);
        }
        this.trainService.setTrainSelections(this.selectedOption);
        this.filterTrains(
            !!(this.isRoundTrip && this.filterQueryParams.onwardId && this.filterQueryParams.returnId) ||
                !!(!this.isRoundTrip && this.filterQueryParams.onwardId)
        ).subscribe(() => {});
    }

    private onOnwardChange(selectedTrain: TrainSelection): void {
        this.selectedOption = {
            ...this.selectedOption,
            onward: selectedTrain,
        };

        this.filterQueryParams.onwardId = selectedTrain.selected ? selectedTrain.id : '';
        this.showDetails.onwardJourney = selectedTrain.showDetails || false;
    }

    private onReturnChange(selectedTrain: TrainSelection): void {
        this.selectedOption = {
            ...this.selectedOption,
            return: selectedTrain,
        };

        this.filterQueryParams.returnId = selectedTrain.selected ? selectedTrain.id : '';
        this.showDetails.returnJourney = selectedTrain.showDetails || false;
    }

    private initSearchParams(): void {
        this.filterQueryParams = {
            sort_by: 'bestValue',
            journey_id: '',
            onwardId: '',
            returnId: '',
        };
    }

    private handleTrainsDataResponse(trainsResponse: TrainsResponse, keepSelected?: boolean): void {
        this.filterQueryParams.refreshFilter = false;

        this.trainsFilter = trainsResponse.ogs;
        this.filterService.filterChange$.next(trainsResponse.ogs);
        this.selectedFiltersCount = ResultsFiltersUtils.selectedFiltersCounter(this.trainsFilter);
        if (keepSelected) {
            if (trainsResponse?.data[1] && trainsResponse.data[1]?.data[0]) {
                this.returnTrainChange$.next(trainsResponse.data[1].data[0]);
            }

            this.trainSections = trainsResponse.data;
            if (!this.trainService.getTrainSelections().onward) {
                if (this.isRoundTrip) {
                    this.trainService.setTrainSelections({
                        onward: TrainUtilsService.buildTrainSelection(this.trainCartData?.items[0].selectedItem),
                        return: TrainUtilsService.buildTrainSelection(this.trainCartData?.items[1].selectedItem),
                        dataId: this.trainCartData?.items[0]?.dataId,
                    } as SelectedOption);
                } else {
                    this.trainService.setTrainSelections({
                        onward: TrainUtilsService.buildTrainSelection(this.trainCartData?.items[0].selectedItem),
                        dataId: this.trainCartData?.items[0]?.dataId,
                    } as SelectedOption);
                }
            }
            if (this.shouldPreserveSelection() || keepSelected) {
                this.loadSelection();
            }
        } else {
            this.trainSections = this.filterTrainsRemoveCartItem(trainsResponse.data);
        }
        this.checkConfirmable();

        const { count, selectedID, increment, limiter } = trainsResponse;
        this.trainListMetadata = {
            count,
            selectedID,
            increment,
        };
        this.start = limiter;
        this.resultsCount$.next(count);
    }

    private filterTrainsRemoveCartItem(trains: TrainsResponseSection[]): TrainsResponseSection[] {
        if (this.trainCartData?.items && this.trainCartData?.items.length > 0) {
            const cartIds: string[] = [this.trainCartData.items[0].selectedItem.id];

            if (this.trainCartData?.items.length > 1) {
                cartIds.push(this.trainCartData.items[1].selectedItem.id);
            }

            return this.trainService.filterTrainsRemoveCartItem(trains, cartIds);
        }

        return trains;
    }

    private loadTrainsData(checkConsistency: boolean = false): void {
        const search = this.searchDataService.getSearch(ServiceId.FLIGHT_TRAIN);
        this.totalPassengers = search.passengers.total;
        this.serviceTitle = this.formServiceTitle(ServiceId.FLIGHT_TRAIN, this.twoWayFlight, this.serviceId);
        this.checkResultsAvailable(this.serviceId);
        this.loaderService.skeletonLoading = true;
        this.trainService.searchServiceData(this.serviceId, this.filterQueryParams, checkConsistency).subscribe(
            (trainsResponse: TrainsResponse) => {
                this.trainService.minTrainPrice = trainsResponse.minimalPrice;
                this.isListCompleted = false;
                this.handleTrainsDataResponse(trainsResponse);
                this.loaderService.skeletonLoading = false;
                const searchData = this.searchDataService.getSearchData(this.serviceId);
                if (searchData?.tripCal) {
                    this.isRoundTrip = searchData.tripCal?.twoDates;
                } else {
                    this.isRoundTrip = false;
                }
                this.handleExtraParams();
                if (!trainsResponse.count && !this.filterCount(trainsResponse.ogs)) {
                    this.trainService.checkServiceFailedAndNotify(this.serviceId);
                }
                setTimeout(() => {
                    console.log('set prerender ready');
                    window.prerenderReady = true;
                }, 1500);
            },
            (error: any) => {
                this.loaderService.skeletonLoading = false;
            }
        );
    }

    private filterTrains(keepSelected?: boolean): Observable<TrainsResponse> {
        this.loaderService.skeletonLoading = true;
        return this.trainService.filterServiceData(this.serviceId, this.filterQueryParams, this.trainsFilter).pipe(
            tap((trainsResponse: TrainsResponse) => {
                this.handleTrainsDataResponse(trainsResponse, keepSelected);
                this.loaderService.skeletonLoading = false;
            })
        );
    }

    private getCartServiceData() {
        this.trainCartData = this.trainService.getSelectedTrainsFromCart();
    }

    private nextIsSummary(): boolean {
        const state = this.router.getCurrentNavigation()?.extras?.state;
        const from = state ? state['from'] : '';
        return from === StateFrom.SUMMARY || from === StateFrom.TAB;
    }

    private showSidebarMobile(): void {
        this.showSidebar = true;
    }

    private checkConfirmable(): void {
        if (this.twoWayFlight) {
            this.confirmable = !!this.selectedOption.onward?.id && !!this.selectedOption.return?.id;
        } else {
            this.confirmable = !!this.selectedOption.onward?.id;
        }
        if (this.confirmable) {
            this.scrollToConfirmationBlock();
        }
    }

    private changeTrainClass(selectedTrain: TrainSelection): void {
        let selectedTrainClass!: TrainClass;

        const selectedTripTrain =
            selectedTrain.journeyType === 'onwardJourney' ? this.selectedOption.return : this.selectedOption.onward;
        if (!selectedTripTrain) {
            return;
        }

        const selectTrainClass = (trainClasses: TrainClass[]) => {
            for (const trainClasse of trainClasses) {
                if (trainClasse.groupId === selectedTrain.groupId) {
                    if (trainClasse.trainClass.tzClass === selectedTrain.tzClass) {
                        if (trainClasse.trainClass.tzSubClass === '') {
                            selectedTrainClass = trainClasse;
                            return;
                        }

                        if (trainClasse.trainClass.tzSubClass === selectedTrain.tzSubClass) {
                            selectedTrainClass = trainClasse; // check for same class available
                            return;
                        }

                        selectedTrainClass = trainClasse; // incase not found matching subClass, choose from available class
                    }

                    if (!selectedTrainClass || selectedTrainClass === null) {
                        selectedTrainClass = trainClasse; //otherwise pick the first available class
                    }
                }
            }
        };

        for (const trainSection of this.trainSections) {
            if (trainSection.section === selectedTrain.journeyType) {
                continue;
            }

            for (const train of trainSection.data) {
                for (const trainClasse of train.trainClasses) {
                    if (trainClasse.id === selectedTripTrain?.classId) {
                        selectTrainClass(train.trainClasses);
                    }
                }
            }
        }

        if (selectedTripTrain && selectedTrainClass) {
            const changedTrainSelection = {
                ...selectedTripTrain,
                classId: selectedTrainClass.id,
                groupId: selectedTrainClass.groupId,
                tripId: selectedTrainClass.tripID,
                tzClass: selectedTrainClass.trainClass.tzClass,
                tzSubClass: selectedTrainClass.trainClass.tzSubClass,
            };

            if (selectedTripTrain.journeyType === 'onwardJourney') {
                this.onOnwardChange(changedTrainSelection);
            } else {
                this.onReturnChange(changedTrainSelection);
            }

            this.trainSelection(selectedTrain);
        }
    }

    private openClassChangeAlert(selectedTrain: TrainSelection): void {
        const journeyType = this.translateService.instant(
            'TRAIN.' + (selectedTrain.journeyType === 'onwardJourney' ? 'RETURN' : 'ONWARD')
        );

        this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant('POPUPS.CLASS_SELECTION.TITLE'),
                multipleContent: [
                    this.translateService.instant('POPUPS.CLASS_SELECTION.MESSAGE', {
                        journeyType: journeyType.toLowerCase(),
                        tzClass: selectedTrain.tzClass,
                    }),
                ],
                cancelBtnTitle: this.translateService.instant('POPUPS.CANCEL'),
                confirmBtnTitle: this.translateService.instant('POPUPS.CONTINUE'),
            })
            .afterClosed()
            .subscribe((changeClass: boolean) => {
                if (!changeClass) {
                    return;
                }

                this.changeTrainClass(selectedTrain);
            });
    }

    private isClassChangeAllowed(selectedTrain: TrainSelection): boolean {
        const enable = true;
        const selected = this.selectedOption;

        if (!selected.onward || selected.onward.id === '' || !selected.return || selected.return.id === '') {
            return enable;
        }

        if (this.filterQueryParams.journey_id === selectedTrain.groupId) {
            return enable;
        }

        return !enable;
    }

    private handleClassChangeOfSelectedTrain(selectedTrain: TrainSelection): void {
        if (
            selectedTrain.journeyType === 'onwardJourney' &&
            (!this.isRoundTrip || !this.selectedOption.return?.selected)
        ) {
            this.selectedOption.return = {} as TrainSelection;
            this.trainSelection(selectedTrain);
            return;
        }

        const allowClassChange = this.isClassChangeAllowed(selectedTrain);
        if (allowClassChange || !selectedTrain.selected) {
            this.trainSelection(selectedTrain);
        } else {
            this.openClassChangeAlert(selectedTrain);
        }
    }

    private loadSelection(): void {
        this.selectedOption = this.trainService.getTrainSelections();
        if (this.selectedOption.onward && this.selectedOption.onward.selected) {
            this.onOnwardChange(this.selectedOption.onward);
            this.filterQueryParams.journey_id = this.selectedOption.onward.groupId;
        }

        if (this.selectedOption.return && this.selectedOption.return.selected) {
            this.onReturnChange(this.selectedOption.return);
            this.filterQueryParams.journey_id = this.selectedOption.return.groupId;
        }
    }

    private setSortingValue(): void {
        const sort = SearchUtilsService.getSortingValue(ServiceId.TRAIN);

        if (sort) {
            this.filterQueryParams.sort_by = sort;
        }
    }

    private scrollToConfirmationBlock(): void {
        setTimeout(() => {
            requestAnimationFrame(() => {
                requestAnimationFrame(() => {
                    this.scrollToElement(this.confirmationComponent, { block: 'end' });
                });
            });
        }, 500);
    }

    private scrollToTrainsList(): void {
        if (this.trainListSkeletonLoaderElementReference) {
            this.scrollToElement(this.trainListSkeletonLoaderElementReference);
        } else {
            this.scrollToElement(this.trainListElementReference);
        }
    }

    private shouldPreserveSelection(): boolean {
        const state = history.state;
        if (state && state.extra) {
            if (state.extra && state.extra.hasOwnProperty('preserveSelection')) {
                return true;
            }
        }
        return false;
    }

    private isChangeOptionsFromBasket() {
        const state = history.state;
        if (state && state.extra) {
            if (state.extra && state.extra.hasOwnProperty('changeOptions')) {
                return true;
            }
        }
        return false;
    }
}
