import { Component, Input, OnInit } from '@angular/core';
import { TrainUtilsService } from '@services/modules/transport/trains/train.utils';

@Component({
    selector: 'tz-trains-section-title',
    templateUrl: './trains-section-title.component.html',
    styleUrls: ['./trains-section-title.component.scss'],
})
export class TrainsSectionTitleComponent implements OnInit {
    @Input() flyType!: string;
    @Input() fromLocation: string | undefined;
    @Input() toLocation: string | undefined;
    @Input() count = 0;
    @Input() selected = false;
    @Input() isRoundTrip = false;

    public title = '';

    constructor(private trainUtilsService: TrainUtilsService) {}

    ngOnInit() {
        this.title = this.trainUtilsService.formSectionTitle(
            this.isRoundTrip,
            this.selected,
            this.flyType,
            this.count,
            this.fromLocation,
            this.toLocation
        );
    }
}
